<template>
  <el-card>
    <el-button
      @click="info.isShow = !info.isShow"
      type="primary"
      icon="el-icon-circle-plus-outline"
      >新增
    </el-button>
    <el-divider />
    <List :seckList="seckList" @updateList="updateList" />
    <Modal :info="info" @updateList="updateList" />
  </el-card>
</template>

<script>
import { mapState, mapActions } from "vuex";
import List from "./List";
import Modal from "./Modal";
export default {
  components: {
    List,
    Modal,
  },
  data() {
    return {
      info: {
        isShow: false,
      },
    };
  },
  computed: {
    ...mapState({ seckList: (state) => state.seck.seckList }),
  },
  methods: {
    ...mapActions("seck", ["getSeckListAction"]),
    updateList() {
      this.getSeckListAction();
    },
  },
  created() {
    this.getSeckListAction();
  },
};
</script>

<style>
</style>


