<template>
  <el-dialog
    @close="reset"
    :title="formData.id > 0 ? '编辑' : '新增'"
    :visible.sync="info.isShow"
  >
    <el-form ref="formRef" label-width="80px" :model="formData">
      <el-form-item label="活动名称 " prop="title">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="活动时间 " prop="title">
        <el-date-picker
          v-model="createTime"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          :start-placeholder="formData.id > 0 ? timeBegin : '开始日期'"
          :end-placeholder="formData.id > 0 ? timeEnd : '结束日期'"
          value-format="timestamp"
          align="right"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="一级分类 " prop="first_cateid">
        <el-select @change="firstCateChange" v-model="formData.first_cateid">
          <el-option
            v-for="item in cateList"
            :key="item.id"
            :label="item.catename"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="二级分类 " prop="second_cateid">
        <el-select v-model="formData.second_cateid">
          <el-option
            v-for="item in secondCateList"
            :key="item.id"
            :label="item.catename"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="选择商品 ">
        <el-select> </el-select>
      </el-form-item> -->
      <el-form-item label="状态 " prop="status">
        <el-radio-group v-model="formData.status">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="reset">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import vm from "../../utils/vm";
import { mapState, mapActions } from "vuex";
export default {
  computed: {
    ...mapState({
      seckList: (state) => state.seck.seckList,
    }),
    ...mapState({
      cateList: (state) => state.cate.cateList,
    }),
  },
  methods: {
    ...mapActions("seck", ["getSeckListAction"]),
    ...mapActions("cate", ["getCateListAction"]),
    firstCateChange(id) {
      const index = this.cateList.findIndex((item) => item.id === id);
      this.secondCateList = this.cateList[index].children;
      console.log(this.secondCateList);
    },
    formatData(val) {
      const date = new Date(Number(val));
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      let h = date.getHours();
      let mm = date.getMinutes();
      let s = date.getSeconds();
      return `${y}-${m}-${d} ${h}:${mm}:${s}`;
    },
    submit() {
      this.formData.begintime = this.createTime[0];
      this.formData.endtime = this.createTime[1];
      // delete this.formData.createTime;
      this.$http.get("/getindexgoods").then((res) => {
        if (res.code === 200) {
          this.formData.goodsid = res.list[0].content[0].id;
        } else {
        }
      });
      const url = this.formData.id > 0 ? "/seckedit" : "/seckadd";
      this.$http.post(url, this.formData).then((res) => {
        if (res.code === 200) {
          this.$success(res.msg);
          this.$emit("updateList");
          this.reset();
        } else {
          this.$error(res.msg);
        }
      });
    },
    reset() {
      this.formData = {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: 1,
        status: "",
      };
      this.createTime = "";
      this.info.isShow = false;
      this.$refs.formRef.resetFields();
    },
  },
  props: ["info"],
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
      value2: "",
      secondCateList: [],
      createTime: [],
      timeBegin: "",
      timeEnd: "",
      formData: {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: 1,
        status: "",
      },
    };
  },
  created() {
    this.getSeckListAction();
    this.getCateListAction();
    this.seckList.goodsid = this.formData.goodsid;
    vm.$on("sendSeckData", (obj) => {
      this.formData = obj;
      let begin = this.formatData(this.formData.begintime);
      let end = this.formatData(this.formData.endtime);
      this.timeBegin = begin;
      this.timeEnd = end;
      this.info.isShow = true;
      this.firstCateChange(obj.first_cateid);
    });
  },
};
</script>

<style>
</style>


