<template>
  <el-table :data="seckList" style="width: 100%">
    <el-table-column align="center" prop="title" label="活动名称">
    </el-table-column>
    <el-table-column align="center" label="开始时间">
      <template v-slot="props">
        {{ props.row.begintime | formatDate }}
      </template>
    </el-table-column>
    <el-table-column align="center" label="结束时间">
      <template v-slot="props">
        {{ props.row.endtime | formatDate }}
      </template>
    </el-table-column>
    <el-table-column align="center" prop="status" label="状态">
      <template v-slot="props">
        <el-tag type="success" v-if="props.row.status === 1">正常</el-tag>
        <el-tag type="danger" v-else>禁用</el-tag>
      </template>
    </el-table-column>
    <el-table-column align="center" prop="status" label="操作">
      <template v-slot="props">
        <el-button
          @click="edit(props.row)"
          type="success"
          circle
          icon="el-icon-edit"
          size="mini"
        ></el-button>
        <el-button
          @click="del(props.row.id)"
          type="danger"
          circle
          icon="el-icon-delete"
          size="mini"
        ></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { mapState, mapActions } from "vuex";
import vm from "../../utils/vm";
export default {
  props: ["seckList"],
  methods: {
    edit(row) {
      vm.$emit("sendSeckData", { ...row });
    },
    del(id) {
      this.$myConfirm(() => {
        this.$http.post("/seckdelete", { id }).then((res) => {
          if (res.code === 200) {
            this.$success(res.msg);
            // 通知父组件更新数据列表
            this.$emit("updateList");
          } else {
            this.$error(res.msg);
          }
        });
      });
    },
  },
};
</script>

<style>
</style>


